import Vue from 'vue'
import VueRouter from 'vue-router'
import Pro from '../views/Pro.vue'
import ProAuth from '../views/ProAuth.vue'
import Filter from '../views/Filter.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    redirect: {
      name: 'Pro'
    }
  },
  {
    path: '/pro',
    name: 'Pro',
    component: Pro
  },
  {
    path: '/proAuth',
    name: 'ProAuth',
    component: ProAuth
  },
  {
    path: '/filter',
    name: 'Filter',
    component: Filter
  }
]

const router = new VueRouter({
  routes
})

export default router
